 <template>
 	<div class="search">
 		<searchBox ref="searchbox" @query="getQuery"></searchBox>
						<div ref="shortcutWraper" v-show="!query" class="shortcutWraper">
						<div class="wrap" ref="wrap">
							<scroll ref='scroll' :refreshDelay='refreshDelay' :data='shortcut' class="shortcut" >
							<div>
								<h1>热门搜索</h1>
							<ul class="hot-key-list">
								<li v-for='item in hotkeys' @click="addQuery(item.k)">
											<div>{{item.k}}</div>
								</li>
							</ul>					
								<div class="search-history" v-show="searchHistory.length">
										<h1 class="title">
											<span class="text">搜索历史</span>
											<span  class="clear" @click="deletall">
												<i class="icon-clear"></i>
											</span>
										</h1>
										<searchHistoryList @delete="deleteone" @select="addQuery" :searches="searchHistory"></searchHistoryList>
									</div>
							</div>
							</scroll>
							</div>
						</div>		 
		 <searchList class='seachResult' ref="seachResult" @select="saveSearch" @listscroll='inputBlur' :query="query" v-show="query"></searchList>
		 <router-view></router-view>
 	</div>
 </template>
 
<script type="text/ecmascript-6">
	import searchBox from 'base/search-box/search-box'
	import {getHotSearch} from 'api/search'
	import {ERR_OK} from 'api/config'
	import searchList from 'components/search-list/search-list'
	import {mapActions,mapGetters} from 'vuex'
	import {playlistMixin,searchMixin} from 'common/js/mixin'
	import searchHistoryList from 'components/searchHistoryList/searchHistoryList'
	import Scroll from 'base/scroll/scroll'
	export default{
		mixins:[playlistMixin,searchMixin],
		data(){
			return {
				hotkeys:[],
				refreshDelay:100
			}
		},
		computed:{
				...mapGetters([
				'searchHistory'
			]),
			 shortcut() {
        return this.searchHistory
      }
		},
		created(){
			this._getHotSearch()
		},
		methods:{
			handlePlaylist(playlist){
      const bottom = playlist.length > 0 ? '60px':''
      // this.$refs.wrap.style.bottom = bottom 
	    // this.$refs.seachResult.style.bottom = bottom 
		
			this.$refs.scroll.refresh()
    },
		
			deletall(){
				this.clearSearchHistory()
			},
			deleteone(item){
				this.deletSearchHistory(item)
			},		
			inputBlur(){
				this.$refs.searchbox.blur()
			},
			addQuery(item){
				this.$refs.searchbox.setQuery(item)
			},
			_getHotSearch(){
				getHotSearch().then((res)=>{
					if(ERR_OK==res.code){
						this.hotkeys = this.getHotKeys(res.data.hotkey)
					}
				})
			},
			getQuery(item){
				this.query = item
			},
			getHotKeys(list){
				const ret = []
				for(var i = 0;i<10;i++){
					let nub = Math.floor(Math.random()*10)
					ret.push(list[nub])
				}
				return ret
			},
		
			...mapActions([
				'saveSearchHistory',
				'deletSearchHistory',
				'clearSearchHistory'
			]),
			// ...mapGetters([
			// 	'playList'
			// ])
		},
		components:{
			searchBox,
			searchList,
			searchHistoryList,
			Scroll
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
	 @import "~common/stylus/variable"
	 
	.search
		position: fixed
		top: 88px
		bottom: 0
		width: 100%
		.hot-key-list
			text-align left
			padding-left 20px
			li
				display inline-block
				line-height 30px
				margin 5px
				padding 5px 10px
				border-radius 7px
				background rgba(0,0,0,0.5)
				a
					color white
		.shortcutWraper
			height 100%
			overflow hidden
			h1
				text-align center
				color $color-text-l
		.search-history
			position: relative
			margin: 0 20px
			.title
				display: flex
				align-items: center
				height: 40px
				font-size: $font-size-medium
				color: $color-text-l
				.text
					flex: 1
				.clear
					extend-click()
					.icon-clear
						color: $color-text-d
			.shortcut
				height 100%
				overflow hidden
		.seachResult
			position relative
		.wrap
			position relative		
</style>


